import isEqual from "lodash-es/isEqual";

export default {
  data() {
    return {
      professores: [],
      turnos: [],
      salas: [],
      materias: [],
      quantidadesAulas: [],
      numero_dias: null,
      professorSelecionado: null,
      diasSemana: [
        { name: "Segunda-feira", id: 0, code: "2A" },
        { name: "Terça-feira", id: 1, code: "3A" },
        { name: "Quarta-feira", id: 2, code: "4A" },
        { name: "Quinta-feira", id: 3, code: "5A" },
        { name: "Sexta-feira", id: 4, code: "6A" },
        { name: "Sábado", id: 5, code: "SA" },
        { name: "Domingo", id: 6, code: "DO" },
      ],
    };
  },
  methods: {
    async getStructure() {
      const respConfig = await this.$axios.get("/configuracao/" + this.id);
      const { turnos, salas, professores, materias, configuracao } =
        respConfig.data;
      this.turnos = turnos;
      this.salas = salas;
      this.professores = professores;
      this.materias = materias;
      this.numero_dias = configuracao.numero_dias;
      if (
        this.turnoSelecionado == null ||
        this.turnos.find((t) => t.id === this.turnoSelecionado) == null
      ) {
        this.turnoSelecionado = this.turnos[0].id;
      }
      if (
        this.professorSelecionado == null ||
        this.professores.find((p) => p.id === this.professorSelecionado) == null
      ) {
        this.professorSelecionado = this.professores[0].id;
      }
      this.getLecturesSum();
    },
    async getLecturesSum() {
      const resp = await this.$axios.get("/aulas/soma/" + this.id);
      const { aulas } = resp.data;
      this.quantidadesAulas = aulas;
    },
    item_classes(item) {
      if (
        item.aula % this.aulasPorDiaTurnoSelecionado ==
          this.aulasPorDiaTurnoSelecionado - 1 &&
        item.aula !== this.aulasPorDiaTurnoSelecionado * 5 - 1
      ) {
        return "ultima";
      }
    },
    saved() {
      this.$emit("saved");
    },
    changed() {
      this.$emit("changed");
    },
    checkChanges(val1, val2) {
      if (!isEqual(val1, val2)) {
        this.changed();
      }
    },
    getClassesFromPeriod(periodId) {
      return periodId != null
        ? this.salas.filter((sala) => sala.turno === periodId)
        : null;
    },
    setLoading(isReload = false) {
      this.$store.dispatch("setLoading", isReload);
    },
    clearLoading() {
      this.$store.dispatch("clearLoading");
    },
    aulasPorDia(idTurno) {
      console.log(this.turnos, idTurno);
      const period = this.turnos.find((t) => t.id === idTurno);
      return period != null ? period.classes : null;
    },
  },
  computed: {
    professoresLookup() {
      const arr = Array.from(this.professores, (professor) => [
        professor.id,
        professor.name,
      ]);
      return Object.fromEntries(arr);
    },
    turnosLookup() {
      if (this.turnos && this.turnos.length) {
        const arr = Array.from(this.turnos, (turno) => [turno.id, turno.name]);
        return Object.fromEntries(arr);
      }
      return null;
    },
    aulasPorDiaTurnoSelecionado() {
      if (!this.turnoSelecionado) return null;
      const period = this.turnos.find((t) => t.id === this.turnoSelecionado);
      return period != null ? period.classes : null;
    },
    salasTurnoSelecionado() {
      return this.getClassesFromPeriod(this.turnoSelecionado);
    },
    headersSalasTurnoSelecionado() {
      const defaultHeaders = [
        {
          text: "Dia",
          width: "10px",
          align: "center",
        },
        {
          text: "Aula",
          value: "aula",
          width: "10px",
          align: "center",
        },
      ];

      return defaultHeaders.concat(
        Array.from(this.salasTurnoSelecionado, (sala) => {
          return { text: sala.name, value: sala.id };
        })
      );
    },
    salasLookup() {
      const arr = Array.from(this.salas, (sala) => [sala.id, sala.name]);
      return Object.fromEntries(arr);
    },
    materiasLookup() {
      const arr = Array.from(this.materias, (materia) => [
        materia.id,
        materia.name,
      ]);
      return Object.fromEntries(arr);
    },
    turnoSelecionado: {
      get() {
        return this.$store.state.selectedPeriod;
      },
      set(value) {
        this.$store.dispatch("setSelectedPeriod", value);
      },
    },
    stringTotalAulasProfessorTurno() {
      if (
        this.professorSelecionado == null ||
        this.turnoSelecionado == null ||
        this.quantidadesAulas == null ||
        !this.quantidadesAulas.length
      ) {
        return null;
      }
      const registro = this.quantidadesAulas.find(
        (qtd) =>
          qtd.id_professor === this.professorSelecionado &&
          qtd.id_turno === this.turnoSelecionado
      );
      if (registro == null) {
        return null;
      }

      const nomeProfessor = this.professoresLookup[this.professorSelecionado];
      if (this.turnos.length === 1) {
        return `${nomeProfessor} - ${registro.quantidade} aulas`;
      } else if (this.turnos.length > 1) {
        const nomeTurno = this.turnosLookup[this.turnoSelecionado];
        return `${nomeProfessor} - ${registro.quantidade} aulas (${nomeTurno})`;
      }
    },
  },
};
